    hatBought: function(type, cost) {
        var username = window.user.username
        if (confirm('Do you want to buy ' + type + ' hat? It will cost you ' + cost + ' coins!')) {
            emit({
                id: 'hatBought',
                u: username,
                t: type,
                c: cost,
            })
        }
    },

