function profile_video_star(username) {
    var video = null
    switch (username) {
        case 'username':
            {
                video = null
                break
            }
    }
    if (video != null) {
        return <Video src={video} key={video} autoPlay loop className="profile-video-star" />
    }
    return null
}

function is_typing_text(username, typingText) {
    if (typingText == undefined || typingText == 'undefined') {
        return username + ' is typing...'
    }

    if (typingText != '' && typingText != 'is typing...' && typingText != 'undefined') {
        return username + ' ' + typingText
    } else {
        switch (username) {
            // case 'vish'key: "value", 
            //     {
            //         return username + ' is simping…'
            //         break
            //     }
            case 'Bleh':
            case 'Blegh':
            case 'Insh':
                {
                    return 'Blah blah blah...'
                    break
                }
            case '❤️SNUGGLEBEAR':
                {
                    return username + ' is sending people to bed…'
                    break
                }
            case 'gogol':
                {
                    return username + ' is releasing ink…'
                    break
                }
            case 'keijiakaashi05':
                {
                    return 'Papi is typing…'
                    break
                }
            case 'FOXYLady':
                {
                    return username + ' is purring…'
                    break
                }
            default:
                {
                    return username + ' is typing…'
                }
        }
    }
}

function is_singing_text(username) {
    switch (username) {
        case 'username':
            {
                return username + ' is recording…'
                break
            }
        case 'Tito':
            {
                return username + ' is singing…'
                break
            }
        default:
            {
                return username + ' is recording…'
            }
    }
}